body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 16px;
  color: #434141;
  
  background-image: url(../assets/img.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  justify-content: center;
}

h1 {
  text-align: center;
  margin: 0.7rem;
}
h4 {
  color: #434141;
  margin: 0.4rem;
}

form {
  text-align: center;
  
}

input {
  border: none;
  border-bottom: 1px dotted black;
  margin-left: 0.5em;
  font-size: 1em;
  outline: none;
  background-color: rgb(231, 229, 229);
}

.wrapper {
  max-width: 680px;
  margin: 0 auto;
}

.temp-wrapper {
  text-align: center;
  position: relative;
}

.temp {
  font-size: 20px;
}

.temp-symbol {
  position: absolute;
  top: 3px;
  margin-left: 4px;
}

.temp-date {
  position: absolute;
  bottom: -1em;
  left: 50%;
  transform: translateX(-50%);
  color: rgba(0, 0, 0, 0.5);
}

.main-div {
  background-color: rgb(251, 247, 247); 
  width: 50rem;
  align-items: center;
  margin: auto;
  padding: 0.2rem;
  border-radius: 2rem;
  filter: drop-shadow(-11px 9px 7px #434141);
  margin-top: 15px;
}

.sec-div {
  background-color: rgb(251, 247, 247);
  width: 70rem;
  height: 33rem;
  align-items: center;
  margin: auto;
  padding: 0.2rem;
  border-radius: 2rem;
  filter: drop-shadow(-11px 9px 7px #434141);
  margin-top: 15px;
}